/*--------------------------------------------------------------
# Services
--------------------------------------------------------------*/
.services {
	.icon-box {
		text-align: center;
		padding: 3.8889rem 1.1111rem 3.8889rem 1.1111rem;
		transition: all ease-in-out 0.3s;
		background: #fff;
		width: 100%;
		box-shadow: 0rem 0.2778rem 5rem 0rem rgba(110, 123, 131, 0.05);
		.icon {
			margin: 0 auto;
			width: 5.5556rem;
			height: 5.5556rem;
			display: flex;
			align-items: center;
			justify-content: center;
			transition: ease-in-out 0.3s;
			position: relative;
			i {
				font-size: 2rem;
				transition: 0.5s;
				position: relative;
			}
			svg {
				position: absolute;
				top: 0;
				left: 0;
				height: 5.5556rem;
				width: 5.5556rem;
				path {
					transition: 0.5s;
					fill: #f5f5f5;
				}
			}
		}
		h4 {
			font-weight: 600;
			margin: 0.5556rem 0 0.8333rem 0;
			font-size: 1.2222rem;
			a {
				color: #45505b;
				transition: ease-in-out 0.3s;
			}
		}
		p {
			line-height: 1.3333rem;
			font-size: 0.7778rem;
			margin-bottom: 0;
			overflow-wrap: break-word;
		}
		&:hover {
			border-color: #fff;
			box-shadow: 0rem 0 1.9444rem 0 rgba(0, 0, 0, 0.08);
		}
	}
	.iconbox-blue {
		i {
			color: #47aeff;
		}
		&:hover {
			.icon {
				i {
					color: #fff;
				}
				path {
					fill: #47aeff;
				}
			}
		}
	}
	.iconbox-orange {
		i {
			color: #ffa76e;
		}
		&:hover {
			.icon {
				i {
					color: #fff;
				}
				path {
					fill: #ffa76e;
				}
			}
		}
	}
	.iconbox-pink {
		i {
			color: #e80368;
		}
		&:hover {
			.icon {
				i {
					color: #fff;
				}
				path {
					fill: #e80368;
				}
			}
		}
	}
	.iconbox-yellow {
		i {
			color: #ffbb2c;
		}
		&:hover {
			.icon {
				i {
					color: #fff;
				}
				path {
					fill: #ffbb2c;
				}
			}
		}
	}
	.iconbox-red {
		i {
			color: #ff5828;
		}
		&:hover {
			.icon {
				i {
					color: #fff;
				}
				path {
					fill: #ff5828;
				}
			}
		}
	}
	.iconbox-teal {
		i {
			color: #11dbcf;
		}
		&:hover {
			.icon {
				i {
					color: #fff;
				}
				path {
					fill: #11dbcf;
				}
			}
		}
	}
}
